#showcase {
  height: 300px;
}

#showcase  {
  font-size: 50px;
  line-height: 1.3;
  position: relative;
  animation: heading;
  animation-duration: 3s;
  animation-fill-mode: forwards;
}

@keyframes heading {
  0%   {top: -50px;}
  100% {top: 200px;}
}

#content {
    position: relative;
    animation-name: content;
    animation-duration: 2s;
    animation-fill-mode: forwards;
  }
  
  @keyframes content {
    0%   {left: -1000px;}
    100% {left: 0;}
  }